<template>
    <div v-if="show">
        <div :class="['modal fade modal-fullscreen', show && 'show']" :style="{ display: display ? 'block' : 'none' }" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <!-- <div class="modal-header d-flex align-items-center">
                        <h5 class="modal-title" id="exampleModalCenterTitle" v-text="headline"></h5>
                        <button type="button" class="close" aria-label="Close" @click="hideModal()">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div> -->
                    <div class="modal-body">
                        <div :class="['d-flex align-items-center justify-content-center', $style.loading]" v-if="loading">
                            <img src="../img/preloader-loop.gif">
                        </div>
                        <iframe
                            :key="show"
                            :src="formLink"
                            frameborder='0'
                            @load="formLoaded"
                        ></iframe>
                    </div>
                    <div class="modal-footer">
                        <button :class="[$style.btn, 'btn mr-auto']" @click="hideModal()">
                            <span>Abbrechen</span>
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div :class="['modal-backdrop fade', show && 'show']" v-if="display"></div>
        <div :class="$style.stillAround" v-if="displayStillAround">
            <div :class="['modal fade', showStillAround && 'show']" :style="{ display: displayStillAround ? 'block' : 'none' }" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" role="document">
                    <div class="modal-content">
                        <div class="modal-header d-flex align-items-center">
                            <h5 class="modal-title" id="exampleModalCenterTitle">Noch da?</h5>
                        </div>
                        <div class="modal-body">
                            <p>Um fortzufahren, klicken Sie innerhalb von {{ currentTimerSeconds }} Sekunden auf 'Weiter'.</p>
                        </div>
                        <div class="modal-footer">
                            <button :class="[$style.btn, 'btn']" @click="confirmStillAround()">
                                <span>Weiter</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
    import { Getter } from 'vuex-class';

    @Component({
        props: {
            headline: String
        }
    })
    export default class Modal extends Vue {
        @Prop({ type: Boolean }) fullscreen;

        formLink = ``;
        closeModalTimer = null;

        showStillAround = false;
        displayStillAround = false;

        confirmTimer = null;
        showStillArountTimeout = null;
        confirmTimerSeconds = 60;
        currentTimerSeconds = 0;
        stillAroundAppearMs = 600000;

        loading = false;

        @Getter('modal/getDisplay') display;
        @Getter('modal/getShow') show;

        @Watch('show')
        showChanged(val) {
            if (val) {
                this.loading = true;
                // this.startStillAroundTimeout();
            }
            // else clearTimeout(this.showStillArountTimer);
        }

        mounted() {
            let obn = 0;
            const params = window.location.href.split('?');
            if (typeof params[1] !== 'undefined') {
                params[1].split('&').forEach(item => {
                    const tmp = item.split('=');
                    if (tmp[0] === 'obn') obn = tmp[1];
                });
            }
            this.formLink = `https://online.fleetcor.de/i?l${obn}`;

            // if (this.show) this.startStillAroundTimeout();
        }

        destroyed() {
            this.destroyConfirmTimer();
        }

        hideModal() {
            this.$store.dispatch('modal/hide');
            // this.destroyStillAroundTimeout();
        }

        formLoaded() {
            this.loading = false;
        }

        startStillAroundTimeout() {
            this.destroyStillAroundTimeout();

            this.showStillArountTimeout = setTimeout(() => {
                this.displayStillAround = true;
                setTimeout(() => {
                    this.showStillAround = true;
                    this.currentTimerSeconds = this.confirmTimerSeconds;
                    this.startConfirmTimer();
                }, 150);
            }, this.stillAroundAppearMs);
        }

        destroyStillAroundTimeout() {
            clearTimeout(this.showStillArountTimeout);
            this.showStillArountTimeout = null;
        }

        startConfirmTimer() {
            this.confirmTimer = setInterval(() => {
                if (this.currentTimerSeconds > 0) this.currentTimerSeconds--;
                else this.confirmStillAround(true);
            }, 1000);
        }

        destroyConfirmTimer() {
            clearInterval(this.confirmTimer);
            this.confirmTimer = null;
        }

        confirmStillAround(closeWholeModal) {
            clearInterval(this.confirmTimer);
            this.confirmTimer = null;

            this.showStillAround = false;
            setTimeout(() => {
                this.displayStillAround = false;
                if (closeWholeModal) this.hideModal();
                else this.startStillAroundTimeout();
            }, 150);
        }
    }
</script>

