        span {
            position: relative;
            z-index: 3;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $shell-red;
        }
        
        &::after {
            content: '';
            position: absolute;
            z-index: 1;
            right: -5px;
            bottom: -5px;
            width: 100%;
            height: 100%;
            background-color: rgba($shell-grey, 0.4);
        }
    }
</style>

